.header {
  width: 100%;
  display: flex;
  align-items: center;
  font-size: 1.125rem;
  padding: 0.8rem 0;
  background: rgb(0 0 0 / 50%);

  @media (max-width: 768px) {
    min-height: 55px;
  }

  &-sticky {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 999;
    box-shadow: 0 2px 24px 0 rgb(0 0 0 / 15%);
    background-color: #383838 !important;
    animation: 500ms ease-in-out 300s normal none 1 running fadeInDown;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
    font-size: 1rem;
  }

  &__navbar {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    &_link {
      margin-right: 1rem;
    }
  }

  &__container {
    display: flex;
    width: 100%;
    justify-content: space-between;

    @media (max-width: 768px) {
      justify-content: initial;
    }
  }
}
